header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ******************** CTA ******************** */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ******************** SOCIALS ******************** */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary)
}

/* ******************** ME IMG ******************** */
.me {
    width: 28rem;
    height: 28rem;
    position: relative;
    left: calc(50% - 13rem);
    filter: drop-shadow(10px 10px 10px black);
    overflow: hidden;
    padding: 1.5rem;
}

/* ******************** SCROLL DOWN ******************** */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
    
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials, 
    .scroll__down {
        display: none;
    }

}